.signedout-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-right: 40px;
    #signup-link {
        width: 150px;
        height: 33.59px;
        background: #F8F1DD;
        border-radius: 16px;
        text-align: center;
        text-decoration: none;
        font-family: 'Goudy Bookletter 1911';
        font-style: normal;
        font-weight: normal;
        font-size: 25px;
        line-height: 34px;
        color: #333333;
    }
    #signin-link {
        width: 150px;
        height: 33.59px;
        background: #A7CBB6;
        border-radius: 16px;
        text-align: center;
        text-decoration: none;
        font-family: 'Goudy Bookletter 1911';
        font-style: normal;
        font-weight: normal;
        font-size: 25px;
        line-height: 34px;
        color: #333333;
    }
}